<!--
 * @Descripttion: 用户管理列表
 * @version: 
 * @Author: 曾元球
 * @Date: 2019-06-20 09:05:19
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-12 15:14:16
 -->

<template>
  <div class='page flex-v'>
    <div class='head flex-v-center flex-justify'>
      <div class='head-title'>用户维护</div>
      <div class='head-btn'
        v-if='btns.hasInsert'
        data-btn-id='106004001'
        @click='openDialog("insert")'>
        <img src='@/assets/img/add.png'
          alt='登记' />&nbsp;<span>新增</span>
      </div>
    </div>
    <div class='query-form'>
      <el-form inline
        size='small'
        class='default-form-style'>

        <el-form-item label='用户名：'>
          <el-input placeholder='请输入'
            v-model.trim='querys.accountNo'
            clearable></el-input>
        </el-form-item>

        <el-form-item label='姓名：'>
          <el-input placeholder='请输入'
            v-model='querys.realName'
            clearable></el-input>
        </el-form-item>
        <el-form-item label='所属机构名称：'>
          <el-select v-model="querys.createOrganizationId"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon='el-icon-search'
            round
            @click='$refs.pagination.initPageList(1)'>查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class='table-box flex-item'>
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type="index">
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label="机构名称"
          prop="orgName"
          show-overflow-tooltip></el-table-column>
        <el-table-column label='姓名'
          prop='realName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='用户名'
          prop='accountNo'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='科室'
          show-overflow-tooltip>
          <template v-slot='{row}'>{{row.deptList | handleDeptName}}</template>
        </el-table-column>
        <el-table-column label='职称'
          prop='title'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='状态'
          prop='status'
          width="100">
          <template v-slot='{row}'>
            <span :class="[row.status == 1 ? 'status-enable' : 'status-disable']"></span>
          </template>
        </el-table-column>
        <el-table-column label='分配角色'
          width='100'>
          <template v-slot='{row}'>
            <a class='link'
              @click='openAssignDialog(row)'>分配</a>
          </template>
        </el-table-column>
        <el-table-column label='操作'>
          <template v-slot='{row}'>
            <i class='iconfont bj-search'
              @click='openDialog("see", row)'></i>
            <i class='iconfont bj-edit'
              v-if='btns.hasUpdate&&row.status==1'
              data-btn-id='106004002'
              @click='openDialog("update", row)'></i>
            <i class='iconfont bj-delete'
              v-if='btns.hasDel&&row.status==1'
              data-btn-id='106004003'
              @click='delConfirm(row)'></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class='foot'>
      <bj-pagination queryUrl='/manageUser/getAllByCondition'
        :queryObj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref='pagination' />
    </div>

    <el-dialog :visible.sync='dialogIsShow'
      custom-class='my-dialog'
      class='flex'
      width='500px'
      :title='dialogTitles[dialogType]'
      :close-on-click-modal='false'
      center>
      <template v-if='dialogType==="see"'>
        <div class='flex'>
          <el-form size='small'
            class='see-style'
            label-width='auto'>
            <el-form-item label='用户名：'>{{dataInfo.accountNo}}</el-form-item>
            <el-form-item label='职称：'>{{dataInfo.title}}</el-form-item>
            <el-form-item label='性别：'>
              {{
                dataInfo.gender == 1 ? '男' : 
                dataInfo.gender == 2 ? '女' :
                '不详'
              }}
              <!-- (dataInfo.gender | emptyData) -->
            </el-form-item>
            <el-form-item label='姓名：'>{{dataInfo.realName}}</el-form-item>
            <el-form-item label='身份证号码：'>{{dataInfo.idCard}}</el-form-item>
            <el-form-item label='机构名称：'>{{orgName}}</el-form-item>
            <el-form-item label='科室名称：'>{{deptNames}}</el-form-item>
            <el-form-item label='是否启用：'>{{dataInfo.status == 1 ? '是' : '否'}}</el-form-item>
          </el-form>
        </div>
        <div slot='footer'>
          <el-button class='btn-hollowing'
            @click='dialogIsShow = false'>取消</el-button>
          <el-button class='btn-info'
            @click='openDialog("update")'>修改</el-button>
        </div>
      </template>
      <template v-else>
        <el-form size='small'
          label-width='auto'
          :model='formFields'
          :rules='rules'
          ref='form'>
          <el-form-item label='用户名：'
            prop='accountNo'>
            <el-input placeholder='请输入'
              :disabled='dialogType==="update"'
              v-model.trim='formFields.accountNo'
              maxlength='31'></el-input>
          </el-form-item>
          <el-form-item label='职称：'
            prop='title'>
            <el-input placeholder='请输入'
              v-model='formFields.title'
              maxlength='31'></el-input>
          </el-form-item>

          <el-form-item label='性别：'
            prop='gender'>
            <el-select v-model='formFields.gender'
              class='block'>
              <el-option :value='1'
                label='男'></el-option>
              <el-option :value='2'
                label='女'></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label='登录名：'
            prop='realName'>
            <el-input placeholder='请输入'
              v-model='formFields.realName'
              maxlength='16'></el-input>
          </el-form-item>

          <el-form-item label='密码：'
            prop='passwords'>
            <el-input placeholder='请输入'
              v-model.trim='formFields.passwords'
              ref='pwd-input'
              autocomplete='new-password'
              :type='inputType'
              @focus='pwdFocus'
              @blur='pwdBlur'
              maxlength='17'>
              <i class='iconfont hand-shape'
                :class='[inputType === "text" ? "bj-show" : "bj-hide"]'
                @click='changeInputType'
                slot='suffix'
                style='margin-right:5px'></i>
            </el-input>
          </el-form-item>

          <el-form-item label='身份证号码：'
            prop='idCard'>
            <el-input placeholder='请输入'
              v-model='formFields.idCard'
              maxlength='18'></el-input>
          </el-form-item>

          <el-form-item label='机构名称：'
            prop='orgGuid'>
            <el-select v-model='selectedOrgInfo'
              class='block'
              value-key="id"
              @change='changeOrgGuid'>
              <el-option v-for='item in orgList'
                :key='item.sysGuid'
                :value='{id:item.sysGuid,type:item.otype}'
                :label='item.oname' />
            </el-select>
          </el-form-item>
          <el-form-item label='科室名称：'
            prop='deptLists'>
            <el-tooltip :content='deptNames'
              placement='top'
              class='tip'
              :disabled='!deptNames'>
              <el-select v-model='formFields.deptLists'
                class='block'
                multiple
                collapse-tags
                value-key='sysGuid'>
                <el-option v-for='item in deptLists'
                  :key='item.sysGuid'
                  :value='{sysGuid: item.sysGuid, dname: item.dname}'
                  :label='item.dname'></el-option>
              </el-select>
            </el-tooltip>
          </el-form-item>

          <el-form-item label='是否启用：'
            prop='status'>
            <el-select v-model='formFields.status'
              class='block'
              placeholder='启用'>
              <el-option :value='1'
                label='启用'></el-option>
              <el-option :value='2'
                label='禁用'></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot='footer'>
          <el-button class='btn-hollowing'
            @click='dialogIsShow = false'>取消</el-button>
          <el-button class='btn-info'
            @click='onSubmit'
            :loading='submitLoading'>保存</el-button>
        </div>
      </template>
    </el-dialog>

    <el-dialog :visible.sync='assignDialogIsShow'
      custom-class='my-dialog'
      class='flex'
      width='500px'
      :title='`分配 - ${realName}`'
      :close-on-click-modal='false'
      center>
      <el-checkbox-group class='green'
        v-model='roleIds'>
        <el-checkbox class='checkbox'
          v-for='(item, i) in roleList'
          :label='item.roleId'
          :key='i'>{{item.roleName}}</el-checkbox>
      </el-checkbox-group>
      <div slot='footer'>
        <el-button class='btn-hollowing'
          @click='assignDialogIsShow = false'>取消</el-button>
        <el-button class='btn-info'
          @click='onSubmitAssign'
          :loading='assignLoading'>保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { lenRule, regIdNo } from '@/lib/utils'
import { mapState } from 'vuex'
const VIRTUAL_PWD = '●●●●●●●●' // 虚拟密码
export default {
  name: 'UserList',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      querys: {
        accountNo: '',
        realName: '',
        createOrganizationId
      },
      formFields: {
        realName: '',
        accountNo: '',
        title: '',
        passwords: '',
        idCard: '',
        orgGuid: '',
        otype: '',
        deptLists: [],
        gender: '',
        status: 1
      },
      selectedOrgInfo: {},
      pageNum: 1,
      pageSize: 10,
      rules: {
        accountNo: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur',
            whitespace: true
          },
          {
            pattern: /^[0-9A-Za-z]+$/,
            message: '用户名只能为数字和字母'
          },
          ...lenRule(30)
        ],
        realName: [
          {
            required: true,
            message: '请输入登录名',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule(15)
        ],
        title: [
          {
            required: true,
            message: '请输入职称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule(30)
        ],
        passwords: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            pattern: /^[^\u4e00-\u9fa5]+$/,
            message: '密码不能带有汉字',
            trigger: 'blur'
          },
          {
            validator(rule, val, cb) {
              if (val === VIRTUAL_PWD) return cb()
              if (/^\d+$/.test(val)) return cb(new Error('密码不能为纯数字'))
              if (/^[a-zA-Z]+$/.test(val)) return cb(new Error('密码不能为纯字母'))
              if (/^[^0-9A-Za-z]+$/.test(val)) return cb(new Error('密码不能为纯符号'))
              return cb()
            },
            // message: '密码至少由两种字符组合',
            trigger: 'blur'
          },
          { min: 8, max: 16, message: '密码长度为8~16位', trigger: 'blur' },
          { max: 16, message: '密码长度为8~16位' }
        ],
        idCard: [
          {
            required: true,
            message: '请输入身份证号码',
            trigger: 'blur',
            whitespace: true
          },
          {
            pattern: regIdNo,
            message: '请输入正确的身份证号码',
            trigger: 'blur'
          }
        ],
        orgGuid: [{ required: true, message: '请选择机构' }],
        deptLists: [{ type: 'array', required: true, message: '请选择科室' }]
      },
      deptLists: [],
      roleList: [],
      roleIds: [],
      dataInfo: {},
      dialogIsShow: false,
      dialogType: '',
      assignDialogIsShow: false,
      realName: '',
      inputType: 'password',
      loading: true,
      submitLoading: false,
      assignLoading: false
    }
  },

  filters: {
    handleDeptName(list) {
      return list.map(item => item.deptName).join('、')
    }
  },

  computed: {
    orgName() {
      if (!this.formFields.orgGuid || !this.orgList.length) return ''
      return this.orgList.find(item => item.sysGuid === this.formFields.orgGuid).oname
    },

    deptNames() {
      return this.formFields.deptLists.length
        ? this.formFields.deptLists.map(item => item.dname).join('、')
        : ''
    },
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('106004001'), // 登记按钮权限
        hasUpdate: ~btnIds.indexOf('106004002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('106004003') // 删除按钮权限
      }
    },
    ...mapState(['orgList'])
  },

  created() {
    this.initStatic()
  },

  methods: {
    // 初始静态数据（不需要响应的数据）
    initStatic() {
      this.dialogTitles = { see: '查看', insert: '新增', update: '修改' }
    },

    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    delConfirm({ accountNo }) {
      this.$confirm('', {
        title: '确定删除用户？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/manageUser/removeOneUser', { accountNo })
            .then(res => {
              this.$message.success('删除成功')
              if (this.tableData.length > 1) {
                return this.$refs.pagination.initPageList()
              }
              let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
              this.$refs.pagination.initPageList(pageNum)
            })
            .catch(err => {
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    },

    openDialog(type, data) {
      this.dialogIsShow = true
      this.dialogType = type
      this.inputType = 'password'
      if (data) {
        this.getDept(data.orgGuid)
        this.selectedOrgInfo = { id: data.orgGuid, type: data.otype }
        this.dataInfo = data
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = data[key]
        })
        this.formFields.deptLists = data.deptList.map(item => {
          return { sysGuid: item.deptGuid, dname: item.deptName }
        })
        this.formFields.passwords = VIRTUAL_PWD
      } else if (type === 'insert') {
        delete this.formFields.sysGuid
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = ''
        })
        this.selectedOrgInfo = {}
        this.formFields.status = 1
        this.formFields.deptLists = []
      }
      this.dialogType !== 'see' && this.$nextTick(() => this.$refs.form.clearValidate())
    },

    openAssignDialog({ accountNo, realName }) {
      this.realName = realName
      this.assignDialogIsShow = true
      this.accountNo = accountNo
      this.$post('/manageUser/getValidRoleList', { accountNo })
        .then(({ data }) => {
          this.roleList = data.allRoleList
          this.roleIds = data.possessRoleList.map(item => item.roleId)
        })
        .catch(err => this.$message.error(err))
    },

    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.submitLoading = true
        let url = `/manageUser/${this.dialogType}ManageUser`
        let msg = this.dialogTitles[this.dialogType]
        let fields = this.handleFormFields(this.formFields)
        let { accountNo, realName } = fields
        this.$post(url, fields)
          .then(res => {
            this.updateCurrentUser(accountNo, realName)
            this.submitLoading = false
            this.dialogIsShow = false
            this.$refs.pagination.initPageList()
            this.$message.success(`${msg}成功`)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },

    // 更改当前用户更新信息
    updateCurrentUser(userId, name) {
      let userInfo = this.$store.getters.o_userinfo,
        { accountNo, realName } = userInfo,
        _name = name.trim()
      if (userId === accountNo && realName !== _name) {
        this.$store.commit('SET_USERINFO', { ...userInfo, realName: _name })
      }
    },

    // 提交分配角色
    onSubmitAssign() {
      if (!this.roleIds.length) return this.$message.warning('至少选择一种角色')
      this.assignLoading = true
      this.$post('/manageUser/allotRole', {
        accountNo: this.accountNo,
        roles: this.roleIds.toString()
      })
        .then(res => {
          this.assignLoading = false
          this.assignDialogIsShow = false
          this.$refs.pagination.initPageList()
          this.$message.success('分配角色成功')
        })
        .catch(err => {
          this.assignLoading = false
          this.$message.error(err)
        })
    },

    // 处理字段
    handleFormFields(data) {
      let deptLists = JSON.stringify(this.formFields.deptLists)
      let passwords = data.passwords === VIRTUAL_PWD ? '' : data.passwords
      return this.$trimStr({ ...data, passwords, deptLists })
    },

    // 密码框的显/隐处理
    changeInputType() {
      this.inputType = this.inputType === 'text' ? 'password' : 'text'
      if (this.dialogType === 'update') {
        if (this.inputType === 'text' && this.formFields.passwords === VIRTUAL_PWD) {
          this.formFields.passwords = ''
        }
        if (this.inputType === 'password' && this.formFields.passwords === '') {
          this.formFields.passwords = VIRTUAL_PWD
        }
      }
    },

    pwdFocus() {
      if (this.dialogType === 'update' && this.formFields.passwords === VIRTUAL_PWD)
        this.formFields.passwords = ''
    },

    pwdBlur() {
      if (this.dialogType === 'update' && this.formFields.passwords === '')
        this.formFields.passwords = VIRTUAL_PWD
    },

    // 机构改变
    changeOrgGuid({ id, type }) {
      this.deptLists = []
      this.formFields.deptLists = []
      this.formFields.orgGuid = id
      this.formFields.otype = type
      this.$nextTick(() => this.$refs.form.clearValidate('deptLists'))
      this.getDept(id)
    },

    // 获取科室
    getDept(organizationGuid) {
      this.$post('/sys/dept/listPaging', {
        pageNum: '1',
        pageSize: '10000',
        enabled: '1', // 只查启用的科室
        organizationGuid
      })
        .then(({ data }) => {
          this.deptLists = data.list
        })
        .catch(err => this.$message.error(err))
    },

    // 获取角色
    getAllRole() {
      if (this.roleList.length) return
      this.$post('/role/getRoleByCondition', {
        pageNum: '1',
        pageSize: '1000',
        status: '1' // 只查启用的角色
      })
        .then(({ data }) => {
          this.roleList = data.list
        })
        .catch(err => this.$message.error(err))
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}

.table-box {
  padding: 0 20px;
  overflow: hidden;
}
.see-style {
  margin: 0 auto;
  max-width: 100%;
}
.checkbox {
  margin-bottom: 10px;
}
</style>



